      padding-top: 40px;
      outline-offset: -40px; } }
  @media only screen and (max-width: 960px) {
    .mainFrame {
      padding-top: 20px;
      outline-offset: -20px; } }
  .mainFrame .contentContainer {
    outline: 1px solid #ff00ff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
    flex-flow: column;
    max-width: 1280px;
    margin: auto;
    padding: 80px;
    width: 100%; }
    @media only screen and (max-width: 1280px) {
      .mainFrame .contentContainer {
        padding: 40px; } }
    @media only screen and (max-width: 960px) {
      .mainFrame .contentContainer {
        padding: 20px; } }



