body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.page-contacts {
  color: darkgoldenrod; }
  .page-contacts.appear_before {
    opacity: 0;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s; }
    .page-contacts.appear_before h1 {
      -webkit-transform: translate(50px, 0);
      -ms-transform: translate(50px, 0);
      transform: translate(50px, 0);
      -webkit-transition: .3s;
      -o-transition: .3s;
      transition: .3s; }
    .page-contacts.appear_before h2 {
      -webkit-transform: translate(0, 50px);
      -ms-transform: translate(0, 50px);
      transform: translate(0, 50px);
      -webkit-transition: .3s;
      -o-transition: .3s;
      transition: .3s; }
    .page-contacts.appear_before h3 {
      -webkit-transform: translate(50px 50px);
      -ms-transform: translate(50px 50px);
      transform: translate(50px 50px);
      -webkit-transition: .3s;
      -o-transition: .3s;
      transition: .3s; }
  .page-contacts.appear_after {
    opacity: 1; }
    .page-contacts.appear_after h1 {
      -webkit-transform: translate(0);
      -ms-transform: translate(0);
      transform: translate(0); }
    .page-contacts.appear_after h2 {
      -webkit-transform: translate(0);
      -ms-transform: translate(0);
      transform: translate(0); }
    .page-contacts.appear_after h3 {
      -webkit-transform: translate(0);
      -ms-transform: translate(0);
      transform: translate(0); }



