body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.project .firstScreen {
  height: 100%;
  color: #FFF;
  display: -ms-flexbox;
  display: flex; }

.project .otherScreen {
  background-color: #FFFFFF;
  min-height: 100vh;
  color: #131517;
  height: 100%; }

.project.appear_before {
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 0;
  margin-top: 30px; }

.project.appear_after {
  opacity: 1;
  margin-top: 0px; }



