body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.headerLogo {
  position: fixed;
  left: 80px;
  top: 80px;
  height: 100%;
  max-height: 54px;
  width: 48.5px; }
  @media only screen and (max-width: 1280px) {
    .headerLogo {
      left: 40px;
      top: 40px; } }
  @media only screen and (max-width: 960px) {
    .headerLogo {
      left: 20px;
      top: 20px; } }
  .headerLogo #svgLogo {
    position: absolute; }
  .headerLogo.appear_before {
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    margin-left: -50px;
    opacity: 0; }
  .headerLogo.appear_after {
    margin-left: 0;
    opacity: 1; }
  .headerLogo.leave_animation {
    margin-left: -50px !important;
    opacity: 0 !important; }



