body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

.footer {
  position: fixed;
  bottom: 80px;
  right: 80px; }
  @media only screen and (max-width: 1280px) {
    .footer {
      bottom: 40px;
      right: 40px; } }
  @media only screen and (max-width: 960px) {
    .footer {
      bottom: 20px;
      right: 20px; } }



