@import url("https://fonts.googleapis.com/css?family=Montserrat:500,700");
body, html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  line-height: inherit;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  font-family: sans-serif; }

#app {
  position: absolute;
  left: 0;
  right: 0;
  overflow: hidden;
  top: 0;
  bottom: 0;
  background-color: #131517;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  line-height: 40px;
  font-size: 20px; }
  #app h1 {
    font-weight: 700;
    font-size: 84px;
    line-height: 80px; }
  #app h2 {
    font-size: 77px;
    line-height: 80px; }
  #app h3 {
    font-size: 51px;
    line-height: 60px; }
  #app h4 {
    font-size: 35px;
    line-height: 60px; }
  #app h5 {
    font-size: 20px;
    line-height: 26px; }
  #app h6 {
    font-size: 15px;
    line-height: 30px; }



